import { ColorPicker, Divider, InputNumber, Segmented, Select, Switch } from "antd";
import React, { useState } from "react";
import { useAppContext } from "../../AppContext";
import { dataSetsNames, style } from "../../core/appConfig";
import Icon from "@ant-design/icons";

import { ReactComponent as circleRed } from '../../icons/circleRed.svg';
import { ReactComponent as dot } from '../../icons/dot.svg';
import { ReactComponent as waste } from '../../icons/waste.svg';
import { ReactComponent as dotblue } from '../../icons/dotBlue.svg';
import { ReactComponent as circle } from '../../icons/circle.svg';
import { SegmentedValue } from "antd/es/segmented";


const LayerCard: React.FC<{ id: any }> = (props: { id: any }) => {
    const { actions, setActions } = useAppContext();
    const [sourceId, setSourceId] = useState('');
    const layer = actions.layers.filter(l => l.id === props.id)[0];
    const filteredOptions = sourceId === '' ? dataSetsNames : [];
    const layertype = layer.style.type;

    const setSelectedItems = (items: any) => {
        layer.style.sourceId = items[items.length - 1] || '';
        const datasets = actions.layers.map(l => l.style.sourceId)
        setActions({ ...actions, layers: actions.layers, index: actions.index + 1, dataSetsNames: datasets.filter((d: any) => d !== '' ? true : false) })
    };

    const setField = (item: any) => {
        layer.style.textField = item;
        setActions({ ...actions, layers: actions.layers, index: actions.index + 1 })
    };

    const setBeforeId = (e: boolean) => {
        layer.style.beforeId = !e ? '' : '417232';
        setActions({ ...actions, layers: actions.layers, index: actions.index + 1 })
    }

    const setCaption = (e: boolean) => {
        layer.style.textFont = e ? 'Noto_Sans_Semibold' : '';
        setActions({ ...actions, layers: actions.layers, index: actions.index + 1 })
    }

    const setTextColor = (value: any, hex: string) => {
        layer.style.textColor = hex;
        setActions({ ...actions, layers: actions.layers, index: actions.index + 1 })
    }

    const setColor = (value: any, hex: string) => {
        layer.style.color = hex;
        setActions({ ...actions, layers: actions.layers, index: actions.index + 1 })
    }

    const setStrokeColor = (value: any, hex: string) => {
        layer.style.strokeColor = hex;
        setActions({ ...actions, layers: actions.layers, index: actions.index + 1 })
    }


    const setIcon = (icon: SegmentedValue) => {
        layer.style.iconImage = icon;
        setActions({ ...actions, layers: actions.layers, index: actions.index + 1 })
    }

    const setFontSize = (size: number | null) => {
        layer.style.textFontSize = typeof size === 'number' ? size : 6;
        setActions({ ...actions, layers: actions.layers, index: actions.index + 1 })
    }

    const setIconSize = (size: number | null) => {
        layer.style.iconWidth = typeof size === 'number' ? size : 16;
        setActions({ ...actions, layers: actions.layers, index: actions.index + 1 })
    }

    const setStrokeWidth = (size: number | null) => {
        layer.style.strokeWidth = typeof size === 'number' ? size : 0;
        setActions({ ...actions, layers: actions.layers, index: actions.index + 1 })
    }

    const setWidth = (size: number | null) => {
        layer.style.width = typeof size === 'number' ? size : 0;
        setActions({ ...actions, layers: actions.layers, index: actions.index + 1 })
    }

    return (
        <div className="layersDiv">
            <span className="rowLayer">
                <span className="title">
                    <span>Layer type:</span></span> <span className="titleLayer">{layer.style.type}</span></span>
            <p />
            <span className="rowLayer">
                <span className="title">
                    Dataset: </span><Select
                    mode="multiple"
                    placeholder="Layer filter by dataset"
                    value={layer.style.sourceId === '' ? [] : [layer.style.sourceId]}
                    onChange={setSelectedItems}
                    style={{ width: '70%' }}
                    options={filteredOptions.map((item) => ({
                        value: item,
                        label: item,
                    }))}
                /></span>
            <Divider />
            <p />
            {layertype === 'line' && <>
                <span className="rowLayer">
                    <span className="title">Color: </span><ColorPicker size="small" value={layer.style.color} onChange={setColor} />
                </span>
                <p />
                <span className="rowLayer">
                    <span className="title">Width: </span><InputNumber
                        min={0}
                        max={10}
                        value={layer.style.width}
                        onChange={setWidth}
                    />
                </span>
            </>}
            {layertype === 'polygon' && <>
                <span className="rowLayer">
                    <span className="title">
                        Color: </span><ColorPicker size="small" value={layer.style.color} onChange={setColor} /></span>
                <p />
                <span className="rowLayer">
                    <span className="title">
                        strokeWidth: </span><InputNumber
                        min={0}
                        max={10}
                        value={layer.style.strokeWidth}
                        onChange={setStrokeWidth}
                    /></span>
                <p />
                <span className="rowLayer">
                    <span className="title">
                        Stroke Color: </span><ColorPicker size="small" value={layer.style.strokeColor} onChange={setStrokeColor} /></span>
            </>}
            {layertype === 'point' && <>
                <span className="rowLayer">
                    <span className="title">
                        Icon:   </span><Segmented
                        value={layer.style.iconImage || ''}
                        onChange={setIcon}
                        options={[
                            { value: 'poi_shootingrange_7_i', icon: <Icon component={circleRed as any} /> },
                            { value: 'ent', icon: <Icon component={dot as any} /> },
                            { value: 'poi_trashbin_1_i', icon: <Icon component={waste as any} /> },
                            { value: 'ent_i', icon: <Icon component={dotblue as any} /> },
                            { value: 'poi_shootingrange', icon: <Icon component={circle as any} /> },
                        ]}
                    /></span>
                <p />
                <span className="rowLayer">
                    <span className="title">
                        Icon size:  </span><InputNumber
                        min={16}
                        max={64}
                        value={layer.style.iconWidth}
                        onChange={setIconSize}
                    /></span>
                <p />
                <span className="rowLayer">
                    <span className="title">
                        Show caption: </span><Switch onChange={setCaption} checked={layer.style.textFont === '' ? false : true} /></span>
                <p />
                <span className="rowLayer">
                    <span className="title">
                        Text color: </span><ColorPicker size="small" value={layer.style.textColor} onChange={setTextColor} /></span>
                <p />
                <span className="rowLayer">
                    <span className="title">
                        Font size:  </span><InputNumber
                        min={6}
                        max={24}
                        value={layer.style.textFontSize}
                        onChange={setFontSize}
                    /></span>
                <p />
                <span className="rowLayer">
                    <span className="title">
                        Text field: </span><Select style={{ width: 120 }}
                            onChange={setField}
                            value={layer.style.textField}
                            options={layer.style.fields.map((item: any) => ({
                                value: item,
                                label: item,
                            }))}
                    /></span>

            </>}
            <p />  <span className="rowLayer">
                <span className="title">
                    Under building position: </span><Switch onChange={setBeforeId} checked={layer.style.beforeId !== '' ? true : false} /></span>
        </div>
    );
};

export default LayerCard;